import { connect } from 'react-redux';
import ChartView from '../components/chartView';
import { addStation, xMinKnown, xMaxKnown } from '../actions/stationActions';

export default connect(
  (state: any, props) => ({
    data: state.station.stationData.heightList,
    weatherData: state.station.stationData.weatherList,
    xMin: state.station.stationData.xMin,
    xMax: state.station.stationData.xMax,
  }),
  (dispatch, props) => ({
    updateXMin: (value: string): void => dispatch(xMinKnown(value)),
    updateXMax: (value: string): void => dispatch(xMaxKnown(value)),
  })
)(ChartView);
