import DateFnsUtils from '@date-io/date-fns';
import AppBar from '@material-ui/core/AppBar';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import { withStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import TimelineIcon from '@material-ui/icons/Timeline';
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from '@material-ui/pickers';
import React, { useEffect } from 'react';
import { Scatter } from 'react-chartjs-2';

import { Color } from '../styles/styles';
import { dateToDayBegin, dateToDayEnd } from '../utils/converter';

export interface Props {
  classes: any;
  data: [];
  weatherData: [];
  xMin: string;
  xMax: string;
  updateXMin: Function;
  updateXMax: Function;
}

const styles: object = theme => ({
  chartContainer: {
    backgroundColor: theme.palette.background.default,
    padding: 8,
    margin: 8,
  },
  chartMenu: {
    backgroundColor: theme.palette.secondary.main,
  },
  chartDatePickerInput: {
    marginLeft: theme.spacing(2),
    color: theme.palette.secondary.contrastText,
    '&:before': {
      borderBottom: `1px solid ${theme.palette.background.default}`,
    },
    '&:after': {
      borderBottom: `2px solid ${theme.palette.background.default}`,
    },
    '&:hover:not(.Mui-disabled)::before': {
      borderBottom: `2px solid ${theme.palette.background.default}`,
    },
  },
  chartDatePicker: {
    marginLeft: theme.spacing(2),
    color: theme.palette.secondary.contrastText,
    '&.Mui-focused': {
      color: theme.palette.secondary.contrastText,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
});

const ChartView = ({
  classes,
  data,
  weatherData,
  xMin,
  xMax,
  updateXMin,
  updateXMax,
}: Props): React.ReactElement => {
  const dateTo = new Date();
  const dateFrom = dateToDayBegin(new Date());
  dateFrom.setDate(dateFrom.getDate());

  const [selectedDateFrom, setSelectedDateFrom] = React.useState<Date | null>(
    dateFrom
  );
  const [selectedDateTo, setSelectedDateTo] = React.useState<Date | null>(
    dateTo
  );
  const handleDateFromChange = (date: Date | null): void => {
    setSelectedDateFrom(dateToDayBegin(date));
    updateXMin(dateToDayBegin(date).toISOString());
  };
  const handleDateToChange = (date: Date | null): void => {
    setSelectedDateTo(dateToDayEnd(date));
    updateXMax(dateToDayEnd(date).toISOString());
  };
  const chartData = {
    labels: ['Point'],
    datasets: [
      {
        label: 'Avlpsniv',
        yAxisID: 'y-axis-1',
        pointStyle: 'circle',
        fill: true,
        pointRadius: 3,
        pointBorderWidth: 3,
        backgroundColor: Color.teal100,
        pointBorderColor: Color.teal100,
        pointBackgroundColor: Color.teal100,
        pointHoverRadius: 5,
        pointHoverBorderWidth: 2,
        pointHoverBackgroundColor: Color.teal900,
        pointHoverBorderColor: Color.teal900,
        pointHitRadius: 10,
        data: data.map((d: any) => {
          return {
            x: new Date(d.id),
            y: d.value,
          };
        }),
      },
      {
        label: 'Utfelling',
        yAxisID: 'y-axis-2',
        fill: true,
        pointStyle: 'rectRot',
        pointRadius: 3,
        pointBorderWidth: 3,
        backgroundColor: Color.purple100,
        pointBorderColor: Color.purple100,
        pointBackgroundColor: Color.purple100,
        pointHoverRadius: 5,
        pointHoverBorderWidth: 2,
        pointHoverBackgroundColor: Color.purple900,
        pointHoverBorderColor: Color.purple900,
        pointHitRadius: 10,
        data: weatherData.map((d: any) => {
          return {
            x: new Date(d.id),
            y: d.precipitation,
          };
        }),
      },
    ],
  };

  const options = {
    scales: {
      xAxes: [
        {
          type: 'time',
          time: {
            unit: 'day',
          },
          ticks: {
            min: new Date(xMin),
            max: new Date(xMax),
          },
        },
      ],
      yAxes: [
        {
          // type: 'linear',
          display: true,
          position: 'left',
          id: 'y-axis-1',
          gridLines: {
            display: false,
          },
          labels: {
            show: true,
          },
          ticks: {
            min: 0,
            callback: (value: number): string => `${value}mm`,
          },
        },
        {
          // type: 'linear',
          display: true,
          position: 'right',
          id: 'y-axis-2',
          gridLines: {
            display: false,
          },
          labels: {
            show: true,
          },
          ticks: {
            min: 0,
            max: 10,
            callback: (value: number): string => `${value}mm/m\u00B2`,
          },
        },
      ],
    },
  };

  const legend = {
    pointStyle: 'star',
  };

  useEffect(() => {
    handleDateFromChange(dateFrom);
    handleDateToChange(dateTo);
  }, []);

  return (
    <div className={classes.chartContainer}>
      <AppBar position="static" className={classes.chartMenu}>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
          >
            <TimelineIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Punktdiagram:avlpsniv
          </Typography>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid container justify="flex-end">
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="dd.MM.yyyy"
                margin="normal"
                id="date-picker-from"
                label="Fra"
                value={selectedDateFrom}
                onChange={handleDateFromChange}
                InputLabelProps={{
                  classes: {
                    root: classes.chartDatePicker,
                  },
                }}
                InputProps={{
                  classes: {
                    root: classes.chartDatePickerInput,
                  },
                }}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                  classes: {
                    root: classes.chartDatePicker,
                  },
                }}
              />
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="dd.MM.yyyy"
                margin="normal"
                id="date-picker-to"
                label="Til"
                value={selectedDateTo}
                onChange={handleDateToChange}
                className={classes.chartDatePicker}
                InputLabelProps={{
                  classes: {
                    root: classes.chartDatePicker,
                  },
                }}
                InputProps={{
                  classes: {
                    root: classes.chartDatePickerInput,
                  },
                }}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                  classes: {
                    root: classes.chartDatePicker,
                  },
                }}
              />
            </Grid>
          </MuiPickersUtilsProvider>
        </Toolbar>
      </AppBar>
      <Scatter data={chartData} options={options} legend={legend} />
    </div>
  );
};

export default withStyles(styles)(ChartView);
